import * as core from '@actions/core';
import Scalafmt from './Scalafmt';
import { ExitCode } from '@actions/core';
async function run() {
    const scalafmtVersion = core.getInput('version');
    const formatFiles = core.getInput('format-files') === 'true';
    const compareBranch = core.getInput('compare-branch');
    const useGitignore = core.getInput('use-gitignore') === 'true';
    const path = core.getInput('path');
    const githubToken = core.getInput('github-token');
    const scalafmt = new Scalafmt(scalafmtVersion);
    const results = await scalafmt.run(path, useGitignore, formatFiles, githubToken, compareBranch);
    results
        .sort((a, b) => a.filename.localeCompare(b.filename))
        .forEach((error) => process.stdout.write(error.toString()));
    if (results.length) {
        process.exitCode = ExitCode.Failure;
    }
}
run().catch((error) => {
    core.error('Unexpected error running Scalafmt');
    core.debug(error);
    core.setFailed(error);
});
